import React from "react";
import images from "../assist/shravan.png";
import bannerimage from '../assist/kumar.jpg';


function About() {
  return (
    <>
      <div className="text-3xl flex justify-center mt-12  underline font-bold font-roboto scroll-smooth " id="about">About Me</div>

      <div style={
            {
                backgroundImage: `url(${bannerimage})` ,
                height: 550
                }
                } className="flex text-white mt-6 px-4 rounded-xl">
        <div  className=" h-40 w-full rounded-3xl px-10 mt-16    ">
          {/* <img className=""    src={sawn}/> */}
          <img  className=" mt-20 shadow rounded-full " src={images} />
        </div>
        <div className="  ">
          <h1 className="text-4xl  font-bold mt-28  shadow px-60 w-full">Full-stack developer</h1>
          <br/>

          <p className="px-10 shadow  font-roboto text-xl"> 
          
            As a recent graduate with a strong academic background in <em className="text-blue-500 underline">Electronics and Communication Engineering</em>, I am deeply passionate about the intersection of electronics and <em className="text-orange-500 underline">software development</em>. I possess a solid foundation in understanding complex systems, thanks to my academic training.

            My primary area of expertise lies in Java programming, and I am currently focused on enhancing my skills as a <em className="text-red-500 underline">Spring Boot Developer</em>. With a keen interest in full-stack development, I am committed to mastering both the <em className="text-green-500 underline">frontend and backend</em> aspects of software development to create robust and efficient solutions.

            I am enthusiastic about applying my knowledge and skills to tackle real-world challenges and contribute to innovative projects in the field of technology.
              </p>
        </div>
      </div>
    </>
  );
}

export default About;
